.container{
    margin-left: 100px;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    gap: 50px;
}
.main{
    width: 300px;
    height: 300px;
    box-shadow: 0px 0px 10px rgb(148, 148, 242);
   
}
.main .content,.date{
    margin-left: 20px;
    color: white;
    font-size: 20px;
}